.nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: 60px;
  align-items: center;

  z-index: 1;
  transition-timing-function: ease-in;
  transition: all 0.3s;
}

.nav_contents {
  display: flex;
  justify-content: space-between;
}

.nav_logo {
  width: 120px;
  position: fixed;
  left: 0;
  cursor: pointer;
  object-fit: contain;
  padding-left: 20px;
}

.nav_black {
  background-color: rgb(12, 12, 12);
}

.nav_avatar {
  right: 20px;
  position: fixed;
  width: 55px;
  cursor: pointer;
  padding:11px
}
